<template>
	<view class="index">
		<button type="warn" @click="enterAccount">开始记账</button>
	</view>
</template>

<script>
	import {mapActions, mapState} from 'vuex';
	export default {
		name: 'index',
		data() {
			return {
				
			}
		},
		watch: {
			openId: {
				handler(){
					this.initAccountList();
				}
			}
		},
		computed: {
			...mapState('publicData', ['appId', 'appSecret', 'openId'])
		},
		onLoad() {
			this.getOpenId();
			this.getInnerCover();
		},
		methods: {
			...mapActions('publicData', ['storeOpenId', 'storeInnerCover', 'storeAccountList']),
			// 初始化用户账本列表
			initAccountList(){
				uniCloud.callFunction({
					name: 'getAllAccount',
					data: {
						openid: this.openId
					},
					success: (res) => {
						this.storeAccountList(res.result);
						uni.hideLoading();
					}
				});
			},
			// 获取所有内置封面的url
			getInnerCover(){
				uniCloud.callFunction({
					name: "getInnerCover",
					data: {
						openid: this.openId
					}
				}).then((res)=>{
					console.log(res.result);
					this.storeInnerCover(res.result);
				});
			},
			// 页面跳转
			enterAccount(){
				uni.redirectTo({
					url:'/pages/rowlist/rowlist',
				});
			},
			// 自动获取openid登录
			getOpenId(){
				uni.showLoading({
					title: '配置用户数据',
					mask: true
				});
				uni.getStorage({
					key: 'openId',
					success: (res)=>{
						this.storeOpenId(res.data);
					},
					fail: ()=>{
						uni.login({
							"provider": "weixin",
							"onlyAuthorize": true,
							success: (event)=>{
								uni.request({
								    url: `https://api.weixin.qq.com/sns/jscode2session?appid=${this.appId}&secret=${this.appSecret}&js_code=${event.code}&grant_type=authorization_code`,
								    success: (res)=>{
										this.storeOpenId(res.data.openid);
										this.cacheOpenId();
								    }
								});
							},
							fail: (err)=>{
								uni.showToast({
									title: '数据配置失败'
								})
						    }
						});
					}
				})
			},
			// 缓存openid
			cacheOpenId(){
				uni.setStorage({
					key: 'openId',
					data: this.openId
				});
			}
		}
	}
</script>

<style lang="scss">
	.index{
		width: 100vw;
		height: 100vh;
		background-image: url(data:image/png;base64,);
		background-repeat: no-repeat;
		background-size: cover;
		overflow: hidden;
		button{
			margin-top: 900rpx;
			width: 45%;
		}
	}
	
</style>
